import React from 'react'
import {useSelector,useDispatch} from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faArrowRight,faShoppingBag} from '@fortawesome/free-solid-svg-icons'

export const SideBar = () => {

    const dispatch = useDispatch()
    const itemsCart = useSelector(state => state.itemsCart)
    const showSideBar = useSelector(state => state.showSideBar)

    const hideSideBar = () => {
        dispatch({
            type:'HIDE_SIDE_BAR'
        })
    }

    const removeItem = (index) => {
        dispatch({
            type:'REMOVE_ITEM',
            index:index
        })
    }

    return(
        <aside className={`m-sideBar color-white ${showSideBar ? 'is-sideBar-visible':''}`}>
            <FontAwesomeIcon icon = {faArrowRight} className="arrow-icon" onClick = {() => {hideSideBar()}}></FontAwesomeIcon>
            {itemsCart.length !== 0 ? 
                <ul>
                    {itemsCart ? itemsCart.map((item,index) => {
                        return <li>{item.name} - {item.price} <button className="color-danger" onClick = {() => {removeItem(index)}}>remover</button></li>
                    }):''}
                </ul>
            :<FontAwesomeIcon className="bag-icon" icon={faShoppingBag}></FontAwesomeIcon>}

        </aside>
    )
}