import React from 'react'
import './Home.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPhone} from '@fortawesome/free-solid-svg-icons'
import {faSearchLocation} from '@fortawesome/free-solid-svg-icons'
import {Footer} from './Footer'
import { Link } from 'react-router-dom'
export const Home = () => {
    return(
        <section className ="l-full-size">
            <div className="m-center home-main-background" title = "bar">
                <div className = "m-center-text m-main-text opacity-animation">
                    <header>
                        <h1>Web Bar</h1>
                    </header>
                    <p>Várias bebidas com o melhor preço!</p>
                    <Link to ="/menu" className ="m-btn xl color-spot-light">Ver Menu</Link>
                </div>
            </div>
            <div className = "m-center home-intermediate m-box-shadow-top">
                <div className ="m-margin-right">
                    <div className ="m-left-text m-main-text m-white-border">
                        <header>
                            <h1>Funcionários comprometios</h1>
                        </header>
                        <p>Nossos funcionários amam o que fazem e tem muita experiência na área</p>
                    </div>
                </div>
            </div>
            <div className ="m-center home-email m-box-shadow-top">
                <div className ="m-margin-right">
                        <div className ="m-left-text m-main-text m-white-border">
                            <header>
                                <h1>Mande um email nos contando sua experiência</h1>
                            </header>
                            <input type="text" placeholder="insira a mensagem" className="home-input"></input> <button className="m-btn md color-spot-light">Enviar</button>
                        </div>
                    </div>
            </div>
            <div className ="m-center color-black home-contact" id="contato">
                <div className ="m-center contact-info">
                    <div className ="m-center-text">
                        <header><h1>Entre em contato</h1></header>
                        <FontAwesomeIcon icon={faPhone}></FontAwesomeIcon>
                        <p>(21) 9999-9999</p>
                    </div>
                </div>

                <div className ="m-center contact-info">
                    <div className ="m-center-text">
                        <header><h1>Localização</h1></header>
                        <FontAwesomeIcon icon={faSearchLocation}></FontAwesomeIcon>
                        <p>Rua faz de conta, 554 - web</p>
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </section>

    )
}