import {createStore} from 'redux'

const INITIAL_STATE = {
    itemsCart: [],
    showSideBar:false
}

function reducer(state = INITIAL_STATE,action){
     switch (action.type){
         case 'ADD_ITEM':
            return state = {
                ...state,
                itemsCart: [...state.itemsCart,action.item]
            }
        case 'SHOW_SIDE_BAR':
            return state = {
                ...state,
                showSideBar: true
           }
        case 'HIDE_SIDE_BAR':
            return state = {
                ...state,
                showSideBar:false
            }
        case 'REMOVE_ITEM':
            return state = {
                ...state,
                itemsCart: state.itemsCart.filter((item,index) => { return index !== action.index ? item : ''})
            }
        
        default:
            return state   
     }
}

export default createStore(reducer,INITIAL_STATE)