import React from 'react'
import { Link } from 'react-router-dom'
import { HashLink} from 'react-router-hash-link';
import {useState,useEffect} from 'react'
import {useSelector,useDispatch} from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingCart} from '@fortawesome/free-solid-svg-icons'

export const Nav = (props) => {

    const itemsCart = useSelector(state => state.itemsCart )
    const [isAnimating,setIsAnimating] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
       setIsAnimating(true)

       setTimeout(() => {
        setIsAnimating(false)
       },300)
    },[itemsCart])

    const showSideBar = () => {
        dispatch({
            type:'SHOW_SIDE_BAR'
        })
    }

    return(
         <nav className="m-nav">
             <ul>
                 <li><Link to="/">Home</Link></li>
                 <li><Link to="/menu">Menu</Link></li>
                 <li><HashLink smooth to="/#contato">Contato</HashLink></li>
             </ul>
             <div className="m-cart" onClick = {() => {showSideBar()}}>
                     <FontAwesomeIcon icon={faShoppingCart}></FontAwesomeIcon>
                     <div className={`cart-count ${isAnimating ? 'scale-animation' : ''}`}>
                         {itemsCart ? itemsCart.length : ''}
                     </div>
            </div>
         </nav>
    )
}