import React,{useState} from 'react'
import './Menu.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGlassCheers} from '@fortawesome/free-solid-svg-icons'
import {Footer} from './Footer'
import whiskyImg from '../assets/whisky.jpg'
import {useDispatch} from 'react-redux'

export const Menu = (props) => {

    const dispatch = useDispatch()

    const [items,setItems] = useState([
        {name:'Glenmorangie Nectar D',price:'R$ 839,00' ,img: whiskyImg},
        {name:'Chivas Regal',price:'R$ 1208,00' ,img: whiskyImg},
        {name:'Dalmore',price:'R$ 1242,00' ,img: whiskyImg},
        {name:'Jack Daniels',price:'R$ 517,00' ,img: whiskyImg},
        {name:'Johnnie Walker Blue Label',price:'R$ 269,00',img:whiskyImg},
        {name:'Laphroaig Quarter Cask',price:'R$ 309,00',img:whiskyImg}
    ])

    const additemToCart = (index) => {
        
        const itemToAdd = items.filter((item,i) => {
            return index === i ? item : '' 
        })

        dispatch({
            type:'ADD_ITEM',
            item : itemToAdd[0]
        })
    }

    return(
        <section className="l-full-size">
            <div className ="m-center menu-main-background"  title = "bebida">
                <div className ="m-center-text  opacity-animation  m-main-text">
                    <header>
                       <h1>Menu</h1>
                    </header>
                    <p>Você encontra a melhor qualidade aqui !</p>
                    <FontAwesomeIcon icon={faGlassCheers}></FontAwesomeIcon>
                </div>
            </div>
            <div className="m-center menu-list-container color-black">
                <div className="m-grid ">
                    {items ? items.map((item,index) => {
                        return <div className="m-card opacity-animation color-white">
                                   <img src={item.img} width="100%" height="120px" alt={item.name} />
                                    <h4>{item.name}</h4>
                                    <p>{item.price}</p>
                                    <button className="m-btn sm color-spot-light" onClick = {()=> {additemToCart(index)}}>Adicionar</button>
                               </div>
                    }):''}
                </div>
            </div>
            <Footer></Footer>
        </section>
    )
}